<template>
  <v-layout row class="pr-2">

    <v-btn icon @click="back">
      <v-icon>chevron_left</v-icon>
    </v-btn>

    <v-spacer/>

    <v-tooltip left :value="quantity">
      <template v-slot:activator="{ on }">
        <v-btn icon @click="openCartDialog">
          <v-badge overlap>
            <template v-slot:badge>
              <span class="fs-16" style="line-height: 16px">
                {{ quantity }}
              </span>
            </template>
            <v-icon>shopping_cart</v-icon>
          </v-badge>
        </v-btn>
      </template>

      <span>Click here to checkout</span>
    </v-tooltip>

  </v-layout>
</template>

<script>
export default {
  computed: {
    quantity() {
      return this.$store.getters['cart/totalItems'];
    },
    tooltip() {
      return this.quantity > 0;
    },
  },

  methods: {
    back() {
      this.$store.commit('toggleMenu');
    },
    openCartDialog() {
      this.$store.state.cartOpen = true;
    },
  },
};
</script>
